<template>
    <div class="box">
        <Header :got="true" :cut="true" :gnt="false"></Header>
        <div class="content">
            <div class="hond">
                <div class="portrait">
                    <ul>
                        <li>头像</li>
                        <li>
                            <input type="file" class="upload" @change="uploadActar" ref="inp">
                            <img class="img" v-if="userImg" :src="'//elm.cangdu.org/img/'+this.$store.state.upAvatar.image_path" />
                            <img v-else src="../images/2.jpg" alt="">
                        </li>
                        <li><van-icon name="arrow"/></li>
                    </ul>
                </div>
                <div class="port">
                    <ul>
                        <li>用户名</li>
                        <li>{{item}}</li>
                        <li><van-icon name="arrow"/></li>
                    </ul>
                </div>
                <div class="port">
                    <router-link to="/anout/receiv">
                        <ul>
                            <li>收货地址</li>
                            <li></li>   
                            <li><van-icon name="arrow"/></li>
                        </ul>
                    </router-link>
                    
                </div>
                <div class="account">
                    账户绑定
                </div>
                <div class="mobile">
                    <ul>
                        <li>
                            <img data-v-c2d94162="" src="data:image/png;base64," alt="">
                        </li>
                        <li>手机</li>
                        <li><van-icon name="arrow"/></li>
                    </ul>
                </div>
                <div class="account">
                    安全设置
                </div>
                <div class="pont">
                    <ul>
                        <li>登录密码</li>
                        <li>修改</li>
                        <li><van-icon name="arrow"/></li>
                    </ul>
                </div>
                <van-button class="danger" @click="del()" type="danger">退出按钮
                    
                </van-button>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import Header from '../header/Header'
    import { Toast } from 'vant';
    export default {
        data () {
            return {
                list:'',
                item:'',
                userImg:"",
                user_id:""
            }
        },
        components: {
            Header
        },
        mounted() {
            let obj = this.$store.state.userInfo
            this.list=obj.avatar
            this.item=obj.username
            this.userImg=this.$store.state.userInfo.avatar
            this.user_id=this.$store.state.userInfo.user_id
        },
        methods: {
            del(){
                Toast.loading({
                        mask: true,
                        message: '退出中...',
                        duration:1000
                    });
                this.$store.commit("del",'')
                this.$router.push("/myuser")
            },
            uploadActar(){
                let input = this.$refs.inp;
                // console.log(input.files[0])
                let data = new FormData();
                data.append('file',input.files[0]);
                let headers = {headers:{"Content-Type":"multipart/from-data"}}
                this.http.post("http://cangdu.org:8001/eus/v1/users/"+this.user_id+"/avatar",data,headers).then((res)=>{
                    // console.log(res)
                    if(res.data.image_path!=""){
                        this.$store.commit('Addvatar',res.data);
                        this.userImg=res.data.image_path;
                    }
                })
                // console.log(this.userImg)
            }
        },
    }
</script>

<style lang="scss" scoped>
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .danger{
        width: 94%;
        margin: 0.5rem 3%;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        .hond{
            width: 100%;
            .portrait{
                width: 100%;
                ul{
                    width: 100%;
                    height: 1.5rem;
                    display: flex;
                    font-size: 0.3rem;
                    border-bottom: 0.01rem solid rgb(230, 219, 219);
                    li{
                        line-height: 1.5rem;
                    }
                    li:nth-of-type(1){
                        flex: 10;
                        margin: 0 0.3rem;
                    }
                    li:nth-of-type(2){
                        flex: 2;
                        padding-top: 0.2rem;
                        .upload{
                            display: block;
                            position: absolute;
                            opacity: 0;
                            // top: 0.35rem;
                            left: 2rem;
                            left: 0;
                        }
                        img{
                            width: 1rem;
                            border-radius:50%; 
                        }
                    }
                    li:nth-of-type(3){
                        color: rgb(209, 197, 197);
                        font-size: 0.3rem;
                        flex: 1;
                    }
                }
            }
            .port{
                width: 100%;
                a{
                    color: black;
                 }
                ul{
                    width: 100%;
                    height: 1.2rem;
                    display: flex;
                    font-size: 0.3rem;
                    border-bottom: 0.01rem solid rgb(230, 219, 219);
                    li{
                        line-height: 1.2rem;
                        
                    }
                    
                    li:nth-of-type(1){
                        flex: 7;
                        margin: 0 0.3rem;
                    }
                    li:nth-of-type(2){
                        color: rgb(209, 197, 197);
                        flex: 3;
                    }
                    li:nth-of-type(3){
                        color: rgb(209, 197, 197);
                        font-size: 0.35rem;
                        flex: 1;
                    }
                }
            }
            .pont{
                width: 100%;
                ul{
                    width: 100%;
                    height: 1.2rem;
                    display: flex;
                    font-size: 0.3rem;
                    border-bottom: 0.01rem solid rgb(230, 219, 219);
                    li{
                        line-height: 1.2rem;
                    }
                    li:nth-of-type(1){
                        flex: 8;
                        margin: 0 0.3rem;
                    }
                    li:nth-of-type(2){
                        color: rgb(209, 197, 197);
                        flex: 1;
                    }
                    li:nth-of-type(3){
                        color: rgb(209, 197, 197);
                        font-size: 0.35rem;
                        flex: 1;
                        padding-top: 0.05rem;
                    }
                }
            }
            .account{
                width: 100%;
                height: 0.8rem;
                line-height: 0.8rem;
                font-size: 0.25rem;
                color: rgb(199, 156, 156);
                padding: 0 0.3rem;
                border-bottom: 0.01rem solid rgb(230, 219, 219);
                }
            }
            .mobile{
                width: 100%;
                ul{
                    width: 100%;
                    height: 1.2rem;
                    display: flex;
                    font-size: 0.3rem;
                    border-bottom: 0.01rem solid rgb(230, 219, 219);
                    li{
                        line-height: 1.2rem;
                    }
                    li:nth-of-type(1){
                        padding: 0.1rem 0.3rem;
                        flex: 1;
                        // .svg{
                        //     font-size:0.3rem;
                        // }
                    }
                    li:nth-of-type(2){
                        // color: rgb(209, 197, 197);
                        flex: 20;
                    }
                    li:nth-of-type(3){
                        color: rgb(209, 197, 197);
                        font-size: 0.35rem;
                        flex: 2;
                    }

                }
            }
        }
</style>


